"use client"
import { getComicsByCharacterId } from "@/lib/api"
import { useEffect, useState } from "react"
import Loader from "../Loader"
import Card from "../Card"

const ComicDetails = ({ characterId }: { characterId: string }) => {
  const [comics, setComics] = useState(null) as any

  useEffect(() => {
    const fetchData = async () => {
      const data = await getComicsByCharacterId(characterId)
      setComics(data)
    }
    fetchData()
  }, [characterId])
  console.log(comics)
  if (!comics) return <Loader />
  return (
    <div className="grid w-full grid-cols-3 gap-4 max-md:gap-2 md:grid-cols-4 lg:grid-cols-5">
      {comics.map((comic: any) => {
        const thumbnailUrl =
          comic.thumbnail.path + "." + comic.thumbnail.extension
        const link = `/comic/${comic.id}`
        return (
          <Card
            key={comic.id}
            title={comic.title}
            link={link}
            thumbnailUrl={thumbnailUrl}
            type="comic"
          />
        )
      })}
    </div>
  )
}

export default ComicDetails
